body,
html {
  margin: 0;
  padding: 0;
  line-height: 1.6;
  font-size: 100%;
  vertical-align: baseline;
  overflow-x: hidden;
}

#app {
  font-family: 'Work Sans', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #714b96;
  /* margin-top: 60px; */
}

.btn-primary,
.btn-outline-primary {
  /* border-color: ; */
  border: 2px solid #714b96;
}

.btn {
  padding: 15px 25px;
  border-radius: 40px;
}

.btn-primary {
  background-color: #714b96;
}

.btn-outline-primary {
  color: white;
}

.row {
  margin: 0;
}

.landing-page {
  color: #fff;
}

h1 {
  font-weight: 900;
  margin-bottom: 40px;
}

p {
  font-size: 22px;
  color: #383838;
}

.container {
  padding: 0;
}

.purple-line {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background-color: #714b96;
  z-index: 1;
}

.container.full-width {
  max-width: 100%;
}

.logo {
  margin-bottom: 0px;
  text-align: center;
}

.logo img {
  width: 280px;
}

.landing-page h1 {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 120px;
  margin-bottom: 10px;
}

.landing-page p {
  color: #fff;
}

.top-background-image {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(/static/img/TopBackground.2c34826.jpeg);
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.top-background-image:after {
  content: ' ';
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: rgba(50, 50, 50, 0.9);
  top: 0;
  left: 0;
}

.landing-page .content.middle {
  width: 1140px;
  padding: 15px;
}

.content.middle {
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  left: 50%;
}

.about,
.cara-mendaftar {
  padding-top: 150px;
  padding-bottom: 150px;
}

.gallery-photo {
  background-size: cover;
  background-position: bottom;
  width: calc(100% / 4);
  height: 250px;
}

.title {
  text-align: center;
  margin-bottom: 100px;
}

.cara-mendaftar {
  background-color: #3c284f;
  color: white;
}

.cara-mendaftar h2 {
  font-weight: 700;
  margin-bottom: 20px;
}

.cara-mendaftar button {
  margin-top: 20px;
}

.cara-mendaftar p {
  color: white;
}

.langkah {
  margin-bottom: 80px;
}

img.icon {
  width: 130px;
}

footer {
  padding-bottom: 50px;
  padding-top: 50px;
}

footer p {
  font-size: 15px;
}

