@font-face {
  font-family: 'Flaticon';
  src: url(data:application/vnd.ms-fontobject;base64,);
  src: url(data:application/vnd.ms-fontobject;base64,?#iefix) format('embedded-opentype'),
    url(data:application/font-woff;base64,) format('woff'),
    url(data:application/x-font-ttf;base64,) format('truetype'),
    url(/static/img/Flaticon.c2b61dd.svg#Flaticon) format('svg');
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: 'Flaticon';
    src: url(/static/img/Flaticon.c2b61dd.svg#Flaticon) format('svg');
  }
}

[class^='flaticon-']:before,
[class*=' flaticon-']:before,
[class^='flaticon-']:after,
[class*=' flaticon-']:after {
  font-family: Flaticon;
  font-size: 20px;
  font-style: normal;
  margin-left: 20px;
}

.flaticon-contract:before {
  content: '\F100';
}

.flaticon-circular-clock:before {
  content: '\F101';
}
